// src/routes/HelpPage.jsx

import { Container, Accordion, Button } from 'react-bootstrap';

function HelpPage() {
  return (
    <Container className="mt-5">
      <h2 className="mb-4"></h2>

      <Accordion defaultActiveKey="0">
        <Accordion.Item eventKey="0">
          <Accordion.Header>  ?</Accordion.Header>
          <Accordion.Body>
               2~3  .      .
          </Accordion.Body>
        </Accordion.Item>

        <Accordion.Item eventKey="1">
          <Accordion.Header>/  ?</Accordion.Header>
          <Accordion.Body>
              7     .
          </Accordion.Body>
        </Accordion.Item>

        <Accordion.Item eventKey="2">
          <Accordion.Header>  ?</Accordion.Header>
          <Accordion.Body>
            ,         .
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>

      <div className="mt-5">
        <h5></h5>
        <p> 09:00 ~ 18:00 ( 12:00 ~ 13:00)</p>

        <h5> </h5>
        <p> : 1544-0000</p>
        <p> : help@seonyoungshoes.com</p>

        <Button variant="primary" className="mt-3">1:1 </Button>
      </div>
    </Container>
  );
}

export default HelpPage;
